* {
    box-sizing: border-box;
}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0;
    padding: 0;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    min-height: 100vh;
    color: #333;
}

/* App Layout */
.app {
    min-height: 100vh;
}

/* Header */
.header {
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(10px);
    padding: 2rem;
    text-align: center;
    box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
    position: sticky;
    top: 0;
    z-index: 100;
}

.header h1 {
    margin: 0 0 0.5rem 0;
    font-size: 2.5rem;
    background: linear-gradient(45deg, #667eea, #764ba2);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.tagline {
    margin: 0 0 1.5rem 0;
    color: #666;
    font-size: 1.1rem;
}

.cart-button {
    background: linear-gradient(45deg, #667eea, #764ba2);
    color: white;
    border: none;
    padding: 1rem 2rem;
    border-radius: 50px;
    font-size: 1.1rem;
    font-weight: bold;
    cursor: pointer;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.cart-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

/* Main Content */
.main {
    padding: 2rem;
}

.movies-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    gap: 2rem;
    max-width: 1400px;
    margin: 0 auto;
}

/* Movie Cards */
.movie-card {
    background: white;
    border-radius: 15px;
    overflow: hidden;
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.12);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    cursor: pointer;
}

.movie-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
}

.movie-image-container {
    position: relative;
    height: 300px;
    overflow: hidden;
}

.movie-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
}

.movie-card:hover .movie-image {
    transform: scale(1.05);
}

.movie-genre {
    position: absolute;
    top: 15px;
    right: 15px;
    background: rgba(0, 0, 0, 0.8);
    color: white;
    padding: 0.5rem 1rem;
    border-radius: 20px;
    font-size: 0.9rem;
    font-weight: bold;
}

.movie-content {
    padding: 1.5rem;
}

.movie-title {
    margin: 0 0 0.5rem 0;
    font-size: 1.4rem;
    font-weight: bold;
    color: #333;
}

.movie-year {
    margin: 0 0 1rem 0;
    color: #666;
    font-size: 0.9rem;
}

.movie-description {
    margin: 0 0 1.5rem 0;
    color: #555;
    line-height: 1.6;
    font-size: 0.95rem;
}

.movie-details {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;
}

.movie-rating {
    background: #f8f9fa;
    padding: 0.5rem 1rem;
    border-radius: 25px;
    font-weight: bold;
    color: #495057;
}

.movie-price {
    font-size: 1.3rem;
    font-weight: bold;
    color: #667eea;
}

/* Buttons */
.btn {
    font: inherit;
    padding: 0.75rem 1.5rem;
    cursor: pointer;
    border-radius: 8px;
    border: none;
    font-weight: 600;
    transition: all 0.2s ease;
    text-align: center;
    display: inline-block;
    text-decoration: none;
}

.add-to-cart-btn {
    width: 100%;
    background: linear-gradient(45deg, #667eea, #764ba2);
    color: white;
    font-size: 1.1rem;
    border-radius: 10px;
    padding: 1rem;
}

.add-to-cart-btn:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.btn--alt {
    background: transparent;
    color: #667eea;
    border: 2px solid #667eea;
}

.btn--alt:hover {
    background: #667eea;
    color: white;
}

.btn--small {
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
}

.btn--large {
    padding: 1rem 2rem;
    font-size: 1.2rem;
}

.btn--danger {
    background: #dc3545;
    color: white;
}

.btn--danger:hover {
    background: #c82333;
}

/* Cart */
.cart {
    position: fixed;
    top: 0;
    right: 0;
    width: 500px;
    height: 100vh;
    background: white;
    z-index: 2000;
    overflow-y: auto;
    box-shadow: -5px 0 25px rgba(0, 0, 0, 0.15);
}

.cart-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    border-bottom: 1px solid #eee;
    background: #f8f9fa;
}

.cart-header h2 {
    margin: 0;
    color: #333;
}

.close-btn {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    color: #666;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.close-btn:hover {
    background: #eee;
}

.cart-empty {
    padding: 3rem 2rem;
    text-align: center;
    color: #666;
}

.cart-items {
    padding: 1rem 0;
}

.cart-item {
    display: flex;
    align-items: center;
    padding: 1rem 2rem;
    border-bottom: 1px solid #eee;
    gap: 1rem;
}

.cart-item-image {
    width: 60px;
    height: 90px;
    object-fit: cover;
    border-radius: 5px;
}

.cart-item-details {
    flex: 1;
}

.cart-item-details h4 {
    margin: 0 0 0.25rem 0;
    font-size: 1rem;
}

.cart-item-year {
    margin: 0 0 0.25rem 0;
    color: #666;
    font-size: 0.85rem;
}

.cart-item-price {
    margin: 0;
    color: #667eea;
    font-weight: bold;
    font-size: 0.9rem;
}

.cart-item-controls {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    align-items: center;
}

.quantity-controls {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.quantity-controls label {
    font-size: 0.85rem;
    color: #666;
}

.quantity-controls select {
    padding: 0.25rem;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.cart-item-total {
    font-weight: bold;
    color: #333;
    min-width: 70px;
    text-align: right;
}

.cart-summary {
    padding: 2rem;
    border-top: 2px solid #eee;
    background: #f8f9fa;
}

.cart-total {
    font-size: 1.3rem;
    margin-bottom: 1.5rem;
    text-align: center;
    color: #333;
}

.checkout-btn {
    width: 100%;
    background: linear-gradient(45deg, #28a745, #20c997);
    color: white;
    margin-bottom: 1rem;
}

.checkout-btn:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 15px rgba(40, 167, 69, 0.3);
}

.cart-note {
    text-align: center;
    color: #666;
    font-size: 0.85rem;
    margin: 0;
    font-style: italic;
}

/* Modal */
.modal {
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
    border-radius: 15px;
    background: white;
    padding: 2rem;
    text-align: center;
    width: 400px;
    z-index: 3000;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.modal-success h3 {
    color: #28a745;
    margin: 0 0 1rem 0;
}

.modal-confirm h3 {
    color: #333;
    margin: 0 0 1rem 0;
}

.modal-actions {
    display: flex;
    gap: 1rem;
    justify-content: center;
    margin-top: 1.5rem;
}

/* Backdrop */
.backdrop {
    position: fixed;
    z-index: 1000;
    background-color: rgba(0, 0, 0, 0.6);
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
}

/* Responsive Design */
@media (max-width: 768px) {
    .header h1 {
        font-size: 2rem;
    }
    
    .movies-grid {
        grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
        gap: 1.5rem;
        padding: 0 1rem;
    }
    
    .cart {
        width: 100%;
    }
    
    .modal {
        width: 90%;
        max-width: 400px;
    }
    
    .main {
        padding: 1rem;
    }
}

@media (max-width: 480px) {
    .header {
        padding: 1rem;
    }
    
    .header h1 {
        font-size: 1.8rem;
    }
    
    .cart-button {
        padding: 0.8rem 1.5rem;
        font-size: 1rem;
    }
    
    .movies-grid {
        grid-template-columns: 1fr;
    }
}

/* Checkout Component Styles */
.checkout {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    width: 90%;
    max-width: 600px;
    max-height: 90vh;
    border-radius: 12px;
    z-index: 2500;
    overflow: hidden;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.checkout-header {
    background: linear-gradient(45deg, #2c3e50, #34495e);
    color: white;
    padding: 1.5rem;
    position: relative;
}

.checkout-header h2 {
    margin: 0;
    font-size: 1.5rem;
}

.checkout-close {
    position: absolute;
    top: 1rem;
    right: 1rem;
    background: none;
    border: none;
    color: white;
    font-size: 1.5rem;
    cursor: pointer;
    padding: 0.5rem;
    border-radius: 50%;
    transition: background-color 0.3s;
}

.checkout-close:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

.checkout-progress {
    display: flex;
    justify-content: space-between;
    margin-top: 1rem;
    padding: 0 1rem;
}

.progress-step {
    flex: 1;
    text-align: center;
    padding: 0.5rem;
    border-radius: 20px;
    font-size: 0.9rem;
    font-weight: 500;
    background-color: rgba(255, 255, 255, 0.2);
    color: rgba(255, 255, 255, 0.7);
    margin: 0 0.25rem;
    transition: all 0.3s;
}

.progress-step.active {
    background-color: #e74c3c;
    color: white;
}

.progress-step.completed {
    background-color: #27ae60;
    color: white;
}

.checkout-body {
    padding: 2rem;
    max-height: 60vh;
    overflow-y: auto;
}

.checkout-form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.form-section {
    background: #f8f9fa;
    padding: 1.5rem;
    border-radius: 8px;
    border: 2px solid transparent;
    transition: border-color 0.3s;
}

.form-section.active {
    border-color: #e74c3c;
    background: #fff;
}

.form-section h3 {
    margin: 0 0 1rem 0;
    color: #2c3e50;
    font-size: 1.2rem;
}

.form-row {
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
}

.form-group {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.form-group label {
    margin-bottom: 0.5rem;
    font-weight: 500;
    color: #2c3e50;
}

.form-group input,
.form-group select,
.form-group textarea {
    padding: 0.75rem;
    border: 2px solid #ddd;
    border-radius: 6px;
    font-size: 1rem;
    transition: border-color 0.3s, box-shadow 0.3s;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
    outline: none;
    border-color: #e74c3c;
    box-shadow: 0 0 0 3px rgba(231, 76, 60, 0.1);
}

.form-group.error input,
.form-group.error select {
    border-color: #e74c3c;
    background-color: #fdf2f2;
}

.error-message {
    color: #e74c3c;
    font-size: 0.875rem;
    margin-top: 0.25rem;
}

.checkout-footer {
    padding: 1.5rem;
    background: #f8f9fa;
    border-top: 1px solid #ddd;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.checkout-total {
    font-size: 1.25rem;
    font-weight: bold;
    color: #2c3e50;
}

.checkout-actions {
    display: flex;
    gap: 1rem;
}

.btn-checkout {
    background: linear-gradient(45deg, #e74c3c, #c0392b);
    color: white;
    border: none;
    padding: 0.75rem 2rem;
    border-radius: 6px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 500;
    transition: all 0.3s;
}

.btn-checkout:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(231, 76, 60, 0.3);
}

.btn-checkout:disabled {
    background: #bdc3c7;
    cursor: not-allowed;
    transform: none;
    box-shadow: none;
}

.btn-back {
    background: #95a5a6;
    color: white;
    border: none;
    padding: 0.75rem 1.5rem;
    border-radius: 6px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s;
}

.btn-back:hover {
    background: #7f8c8d;
}

.payment-processing {
    text-align: center;
    padding: 2rem;
}

.processing-spinner {
    width: 50px;
    height: 50px;
    border: 4px solid #f3f3f3;
    border-top: 4px solid #e74c3c;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin: 0 auto 1rem;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* Order Confirmation Component Styles */
.order-confirmation {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    width: 90%;
    max-width: 700px;
    max-height: 90vh;
    border-radius: 12px;
    z-index: 2500;
    overflow: hidden;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.confirmation-header {
    background: linear-gradient(45deg, #27ae60, #2ecc71);
    color: white;
    padding: 2rem;
    text-align: center;
    position: relative;
}

.confirmation-close {
    position: absolute;
    top: 1rem;
    right: 1rem;
    background: none;
    border: none;
    color: white;
    font-size: 1.5rem;
    cursor: pointer;
    padding: 0.5rem;
    border-radius: 50%;
    transition: background-color 0.3s;
}

.confirmation-close:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

.success-icon {
    font-size: 3rem;
    margin-bottom: 1rem;
}

.confirmation-header h2 {
    margin: 0 0 0.5rem 0;
    font-size: 2rem;
}

.order-number {
    font-size: 1.1rem;
    opacity: 0.9;
    font-weight: normal;
}

.confirmation-body {
    padding: 2rem;
    max-height: 60vh;
    overflow-y: auto;
}

.order-summary {
    background: #f8f9fa;
    padding: 1.5rem;
    border-radius: 8px;
    margin-bottom: 2rem;
}

.order-summary h3 {
    margin: 0 0 1rem 0;
    color: #2c3e50;
}

.order-items {
    margin-bottom: 1.5rem;
}

.order-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.75rem 0;
    border-bottom: 1px solid #ddd;
}

.order-item:last-child {
    border-bottom: none;
}

.item-details {
    flex: 1;
}

.item-name {
    font-weight: 500;
    color: #2c3e50;
}

.item-quantity {
    color: #7f8c8d;
    font-size: 0.9rem;
}

.item-price {
    font-weight: bold;
    color: #e74c3c;
}

.order-total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 1rem;
    border-top: 2px solid #e74c3c;
    font-size: 1.25rem;
    font-weight: bold;
    color: #2c3e50;
}

.customer-info {
    background: #f8f9fa;
    padding: 1.5rem;
    border-radius: 8px;
    margin-bottom: 2rem;
}

.customer-info h3 {
    margin: 0 0 1rem 0;
    color: #2c3e50;
}

.info-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1rem;
}

.info-item {
    display: flex;
    flex-direction: column;
}

.info-label {
    font-weight: 500;
    color: #7f8c8d;
    font-size: 0.9rem;
    margin-bottom: 0.25rem;
}

.info-value {
    color: #2c3e50;
}

.next-steps {
    background: #e8f5e8;
    border: 1px solid #27ae60;
    border-radius: 8px;
    padding: 1.5rem;
}

.next-steps h3 {
    margin: 0 0 1rem 0;
    color: #27ae60;
}

.steps-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1rem;
}

.step-item {
    text-align: center;
    padding: 1rem;
    background: white;
    border-radius: 6px;
    border: 1px solid #bdc3c7;
}

.step-icon {
    font-size: 2rem;
    margin-bottom: 0.5rem;
    color: #27ae60;
}

.step-title {
    font-weight: 500;
    color: #2c3e50;
    margin-bottom: 0.5rem;
}

.step-description {
    font-size: 0.9rem;
    color: #7f8c8d;
}

.confirmation-actions {
    padding: 1.5rem;
    background: #f8f9fa;
    border-top: 1px solid #ddd;
    display: flex;
    justify-content: center;
    gap: 1rem;
}

.btn-primary {
    background: linear-gradient(45deg, #e74c3c, #c0392b);
    color: white;
    border: none;
    padding: 0.75rem 2rem;
    border-radius: 6px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 500;
    transition: all 0.3s;
}

.btn-primary:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(231, 76, 60, 0.3);
}

.btn-secondary {
    background: #95a5a6;
    color: white;
    border: none;
    padding: 0.75rem 2rem;
    border-radius: 6px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 500;
    transition: all 0.3s;
}

.btn-secondary:hover {
    background: #7f8c8d;
    transform: translateY(-2px);
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .checkout,
    .order-confirmation {
        width: 95%;
        max-height: 95vh;
    }
    
    .checkout-body,
    .confirmation-body {
        padding: 1rem;
        max-height: 50vh;
    }
    
    .form-row {
        flex-direction: column;
        gap: 0.5rem;
    }
    
    .checkout-actions,
    .confirmation-actions {
        flex-direction: column;
    }
    
    .info-grid,
    .steps-grid {
        grid-template-columns: 1fr;
    }
    
    .checkout-progress {
        padding: 0 0.5rem;
    }
    
    .progress-step {
        font-size: 0.8rem;
        padding: 0.25rem;
    }
}
